import React from 'react';
import PropTypes from 'prop-types';
import style from './Transport.module.css';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';


const TableComponent = (props) => {
    const { Head, busStopData } = props;
    return (
        <TableContainer component={Paper} className={style['main-table']}>
            <Table className={style['table']} size='medium' padding='default'>
                <TableHead>
                    <TableRow>
                        {Head.map((value) => (
                            <TableCell align="left" key={value}>{value}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {busStopData !== undefined ? Object.entries(busStopData).map((value) => (
                        <TableRow key={value[0]}>
                            <TableCell align="left">{value[0]}</TableCell>
                            <TableCell align="center">{value[1][0]}</TableCell>
                            <TableCell align="right">{value[1][1]}</TableCell>
                        </TableRow>
                    )) : <TableRow />}
                </TableBody>
            </Table>
        </TableContainer>
    );
};

TableComponent.defaultProps = {
    Head: ['Остановка', 'Кол-во сообщений', 'Время'],
};

TableComponent.propTypes = {
    Head: PropTypes.array,
    busStopData: PropTypes.object,
};

export default TableComponent;