import React, {useState} from 'react';
import { Redirect } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import BookIcon from '@material-ui/icons/Book';
import Avatar from '@material-ui/core/Avatar';
import {makeStyles} from "@material-ui/core/styles";
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import TextField from '@material-ui/core/TextField';

const createAccount = async (login, email, password) => {
  const body = JSON.stringify({login: login, email: email, password: password});
  const response = await fetch('http://127.0.0.1:8000/api/create_account', {
    method: 'POST',
    body: body
  });
  return response.json()
};

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(13),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    backgroundColor: 'white',
    borderRadius: '20px',
    padding: '2% 8%'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.success.main,
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const CreateAccount = () => {
  const classes = useStyles();
  const [data, setData] = useState({});
  const [alert, setAlert] = useState({
    state: false,
    msg: ''
  });
  const [redirect, setRedirect] = useState(false);

  const onSubmit = (e) => {
    e.preventDefault();
    createAccount(data.login, data.email, data.password)
        .then((data) => {
          if (data.status !== undefined) {
            setAlert({...alert, state:true, msg: 'Проверьте правильность данных!'});
            setTimeout(() => {
              setAlert({...alert, state:false, msg:''});
            }, 1000)
          }
          if (data.access_token && data.refresh_token) {
            localStorage.setItem('accessToken', data.access_token);
            localStorage.setItem('refreshToken', data.refresh_token);
            setAlert({...alert, state:true, msg: 'Подтвердите адрес электронной почты!'});
            setTimeout(() => {
              setAlert({...alert, state:false, msg:''});
              setRedirect(true);
            }, 2000)
          }
        })
  };

  const onChange = (e) => {
    e.preventDefault();
    const {target} = e;
    setData({ ...data, [target.name]: target.value});
  };

  return (
      <Container component="main" maxWidth="xs">
        {alert.state && <Alert severity="warning">{alert.msg}</Alert>}
        {redirect && <Redirect to='/transport' />}
        <div className={classes.paper}>
          <Avatar className={classes.avatar} style={{margin: '0 auto'}}>
            <BookIcon />
          </Avatar>
          <Typography variant="h5" align='left' gutterBottom>
            Создание аккаунта
          </Typography>
          <form className={classes.form} onSubmit={onSubmit}>
            <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                label="Email"
                name="email"
                type='email'
                id='email'
                autoFocus
                onChange={onChange}
            />
            <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="login"
                label="Login"
                type="text"
                id="login"
                onChange={onChange}
            />
            <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                onChange={onChange}
            />
            <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                className={classes.submit}
            >
              Создать
            </Button>
          </form>
        </div>
      </Container>
  );
};

export default CreateAccount;