import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import style from './Header.module.css';

const LinkToOtherRoute = (props) => {
    const { value, activeClassName, className } = props;
    return (
        <NavLink className={className} activeClassName={activeClassName} to={value.link}>{value.name}</NavLink>
    );
};

LinkToOtherRoute.defaultProps = {
    value: {
        link: '/',
        name: 'AntiController',
    },
    activeClassName: style['is-active'],
    className: style['nav-link'],
};

LinkToOtherRoute.propTypes = {
    value: PropTypes.shape({
        link: PropTypes.string,
        name: PropTypes.string,
    }),
    activeClassName: PropTypes.string,
    className: PropTypes.string,
};

export default LinkToOtherRoute;