import React from 'react';
import PropTypes from 'prop-types';
import LinkToOtherRoute from './LinkToOtherRoute';
import style from './Header.module.css';
import Account from './Account';

const routs = [
    {
        link: '/transport',
        name: 'Транспорт',
    },
    {
        link: '/auth',
        name: 'Войти',
        otherLink: '/profile',
        otherName: 'Профиль',
        needAuth: true,
    },
];

const Header = () => {
    return (
        <header className={style.header}>
            <div className={style.wrapper}>
                <div className={style.content}>
                    <div className={style.logo}>
                        <LinkToOtherRoute
                            key='AntiController'
                            value={{link: '/', name: 'AntiController'}}
                            activeClassName=''
                            className={style['logo-link']}/>
                    </div>
                    <nav>
                        {routs.map((value) => (
                            !(value.needAuth) && <LinkToOtherRoute
                                    key={value.link}
                                    value={{link: value.link, name: value.name}}
                                    className={style[value.link.replace('/', '')]}
                                />))}
                    </nav>
                    <Account />
                </div>
            </div>
        </header>
    );
};

Header.propTypes = {
    auth: PropTypes.bool
};

export default Header;
