import React, {useEffect, useState, useRef} from 'react';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Avatar from '@material-ui/core/Avatar';
import {addBusStop, getCityStops} from './utils'
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import {makeStyles} from "@material-ui/core/styles";
import style from './AddBusStop.module.css'
import Alert from '@material-ui/lab/Alert';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(13),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        backgroundColor: 'white',
        borderRadius: '20px',
        padding: '2% 8%',
        width: '100%'
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.success.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

const AddBusStop = () => {
    const [formData, setFormData] = useState({
        city: 'brest',
        typeBusStop: 'dirty',
        busStopName: 'цум'
    });
    const [alert, setAlert] = useState(false);
    const selectionRef = useRef(null);

    useEffect(() => {
        getCityStops(formData.city)
        .then(json => {
            if (json.detail !== undefined) return;
            if (selectionRef.current !== null){
                if (selectionRef.current.length  !== 0) selectionRef.current.innerHTML='';
                json.map((value) => {
                    selectionRef.current.append(new Option(value, "busStopName"))
                });
                setFormData({
                    ...formData,
                    busStopName: json[0],
                })
            }
        })
    }, [formData.city]);

    const onChange = ({target}) => {
        if (target.type === 'select-one') {
            if (target.length === 0) return;
            const option = target.options[target.selectedIndex];
            setFormData({
                ...formData,
                [option.value]: option.text,
            })
        } else {
            setFormData({...formData, [target.name]: target.value})
        }};

    const onSubmitForm = (e) => {
        e.preventDefault();
        const accessToken = localStorage.getItem('accessToken');
        addBusStop(accessToken, formData.busStopName, formData.city, formData.typeBusStop)
        .then(data => {
            if (data.status) {
                setAlert(false);
            } else {
                setAlert(true);
                setTimeout(() => {
                    setAlert(false);
                }, 1000);
            }
        })
    };

    const classes = useStyles();

    return (
        <Container maxWidth="xs">
            {alert && <Alert severity="warning">Остановка не добавлена.</Alert>}
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <AddIcon />
                </Avatar>
                <h4 className={style.h5}>Добавить остановку</h4>
                <form onSubmit={onSubmitForm}>
                    <div style={{
                        width: '50%'
                    }}>
                        <h5 className={style.h6}>Города</h5>
                        <Select
                            value={formData.city}
                            onChange={onChange}
                            name='city'
                            style={{width: '200%'}}
                        >
                            <MenuItem value='brest'>Брест</MenuItem>
                            <MenuItem value='gomel'>Гомель</MenuItem>
                            <MenuItem value='grodno'>Гродно</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <h5 className={style.h6}>Тип остановки</h5>
                        <Select
                            value={formData.typeBusStop}
                            onChange={onChange}
                            name='typeBusStop'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='dirty'>С контроллерами</MenuItem>
                            <MenuItem value='clean'>Без контроллеров</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <h5 className={style.h6}>Номер автобуса</h5>
                        <select
                            ref={selectionRef}
                            onChange={onChange}
                            className={style['bus-number-select']}
                        />
                    </div>
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        className={classes.submit}
                    >
                        Добавить
                    </Button>
                </form>
            </div>
        </Container>
    );
};

export default AddBusStop;