import {domain} from "../../../config";

export const addBusStop = async (token, busStopName, city, typeBusStop) => {
    const body = JSON.stringify({bus_stop_name: busStopName, city: city, type_bus_stop: typeBusStop});
    const response = await fetch(`${domain}/api/user/add_bus_stop`, {
        method: 'POST',
        body: body,
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': token
        },
    });
    return response.json()
};

export const getCityStops = async (city) => {
    const response = await fetch(`${domain}/api/${city}/city_stops`, {
        method: 'GET'
    });
    return response.json();
};