import React, {useState, useEffect, useRef} from 'react';
import TableComponent from './Table'
import style from './Transport.module.css';
import { getTransportNumber, getBusStopInformation, getDefaultTableOptions } from './utils.js';
import Button from '@material-ui/core/Button';
import BookIcon from '@material-ui/icons/Book';
import Avatar from '@material-ui/core/Avatar';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import {makeStyles} from "@material-ui/core/styles";
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';

const useStyles = makeStyles((theme) => ({
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.success.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

const defaulTableValue = {
    city: 'brest',
    transport_number: '1',
    selection_bus_stop: 'dirty',
    time: 10000,
    sort: 'Время',
    transport_type: 'bus',
    isFirst: true
};

const Transport = () => {
    const [tableOptions, settableOptions] = useState(getDefaultTableOptions(defaulTableValue));
    const [tableValue, setTableValue] = useState();
    const classes = useStyles();
    const selectionRef = useRef(null);

    useEffect(() => {
        getTransportNumber(tableOptions.city, tableOptions.transport_type)
            .then((json) => {
                if (json.detail !== undefined) return;
                if (selectionRef.current !== null){
                    if (selectionRef.current.length  !== 0) selectionRef.current.innerHTML='';
                    json.map((value) => {
                        let option = new Option(value, "transport_number");
                        if (value === tableOptions.transport_number) option.selected = true;
                        selectionRef.current.append(option);
                    });
                    settableOptions({
                        ...tableOptions,
                        transport_number: json[0],
                    })
                }
            });
    }, [tableOptions.city, tableOptions.transport_type]);

    useEffect(() => {
        if (tableOptions.isFirst) {
            const { city, transport_number, selection_bus_stop, time, sort, transport_type } = tableOptions;
            getBusStopInformation(city, transport_number, selection_bus_stop, time, sort, transport_type)
                .then((data) => {
                    if (data.detail !== undefined) return;
                    setTableValue(data);
                });
        }
    }, [tableOptions.isFirst]);

    const onSubmitForm = (e) => {
        e.preventDefault();
        const { city, transport_number, selection_bus_stop, time, sort, transport_type } = tableOptions;
        getBusStopInformation(city, transport_number, selection_bus_stop, time, sort, transport_type)
            .then((data) => {
                localStorage.setItem('tableValue', JSON.stringify(tableOptions));
                setTableValue(data);
            });
    };

    const onChange = ({target}) => {
        if (target.type === 'select-one') {
            if (target.length === 0) return;
            const option = target.options[target.selectedIndex];
            settableOptions({
                ...tableOptions,
                [option.value]: option.text,
            })
        } else {
            settableOptions({...tableOptions, [target.name]: target.value});
        }};

    const onRange = (event, newValue) => {
        settableOptions({...tableOptions, time: newValue});
    };

    return (
        <div className={style.container}>
            <div className={style['main-form']}>
                <Avatar className={classes.avatar} style={{margin: '0 auto'}}>
                    <BookIcon />
                </Avatar>
                <Typography variant="h5" align='left' gutterBottom style={{textAlign: 'center'}}>
                    Информация
                </Typography>
                <form onSubmit={onSubmitForm}>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Города
                        </Typography>
                        <Select
                            value={tableOptions.city}
                            onChange={onChange}
                            name='city'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='brest'>Брест</MenuItem>
                            <MenuItem value='gomel'>Гомель</MenuItem>
                            <MenuItem value='grodno'>Гродно</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип транспорта
                        </Typography>
                        <Select
                            value={tableOptions.transport_type}
                            onChange={onChange}
                            name='transport_type'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='bus'>Автобус</MenuItem>
                            <MenuItem value='trolleybus'>Троллейбус</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип остановки
                        </Typography>
                        <Select
                            value={tableOptions.selection_bus_stop}
                            onChange={onChange}
                            name='selection_bus_stop'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='dirty'>С контроллерами</MenuItem>
                            <MenuItem value='clean'>Без контроллеров</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип сортировки
                        </Typography>
                        <Select
                            value={tableOptions.sort}
                            onChange={onChange}
                            name='sort'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='Время'>Время</MenuItem>
                            <MenuItem value='Сообщения'>Сообщения</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Время
                        </Typography>
                        <Slider
                            defaultValue={tableOptions.time}
                            aria-labelledby="discrete-slider"
                            valueLabelDisplay="auto"
                            step={4000}
                            marks
                            min={1000}
                            max={99999}
                            onChange={onRange}
                        />
                    </div>
                    <div>
                        <h5 className={classes.h6}>Номер автобуса</h5>
                        <select
                            ref={selectionRef}
                            onChange={onChange}
                            className={style['bus-number-select']}
                            name='transport_number'
                        />
                    </div>
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        className={classes.submit}
                    >
                        Добавить
                    </Button>
                </form>
            </div>
            <div>
                <TableComponent busStopData={tableValue} />
            </div>
        </div>
    );
};

export default Transport;