import { PUT_AUTH, REMOVE_AUTH, ADD_ALERT, REMOVE_ALERT } from '../types'
import { tokenLifeCheck } from '../utils/authUtils'
import {domain} from '../../config'


function putAuth() {
    return {
        type: PUT_AUTH,
    }
}

export function removeAuth() {
    return {
        type: REMOVE_AUTH,
    }
}

function addAlert() {
    return {
        type: ADD_ALERT
    }
}

function removeAlert() {
    return {
        type: REMOVE_ALERT
    }
}

export function checkAuth(login, password) {
    return async (dispatch) => {
        const body = JSON.stringify({ "login": login, "password": password });
        const response = await fetch(`${domain}/api/auth`, {
            method: 'POST',
            body: body,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
        });
        const json = await response.json();
        if (json.status === undefined && json.detail === undefined) {
            localStorage.setItem('accessToken', json.access_token);
            localStorage.setItem('refreshToken', json.refresh_token);
            dispatch(putAuth())
        } else {
            dispatch(removeAuth());
            dispatch(addAlert());
            setTimeout(() => {
                dispatch(removeAlert())
            }, 1000)
        }
    }
}

export function checkToken(token) {
    return (dispatch) => {
        const tokenLife = tokenLifeCheck(token);
        if (tokenLife) dispatch(putAuth())
    }
}


export function refreshTokenAction(accessToken, refreshToken) {
    return (dispatch) => {
        if (!tokenLifeCheck(accessToken) && tokenLifeCheck(refreshToken)) {
            dispatch(addAlert());
            fetch(`${domain}/api/refresh_token`, {
                method: 'POST',
                body: JSON.stringify({ "refresh_token": refreshToken }),
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
            })
            .then(response => response.json())
            .then(json => {
                dispatch(removeAlert());
                try {
                    if (json['status'] === undefined) {
                        localStorage.setItem('accessToken', json.access_token);
                        localStorage.setItem('refreshToken', json.refresh_token);
                        dispatch(putAuth())
                    } else {
                        dispatch(removeAuth())
                    }
                } catch (e) {
                    dispatch(removeAuth())
                }
            })
        }
    }
}