import React from 'react'
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux'
import { checkAuth } from '../../redux/Actions/authAction';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(13),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        backgroundColor: 'white',
        borderRadius: '20px',
        padding: '5% 8%'
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    h5: {
        fontWeight: '400',
        fontSize: '2em'
    }
}));

const Auth = ({auth, alert, dispatch}) => {
    const onSubmit = (e) => {
        e.preventDefault();

        let valueForm = {
            login: undefined,
            password: undefined,
        };

        const { target } = e;
        for (let index = 0; index <= target.elements.length; index++) {
            try {
                const element = target.elements[index];
                if (element.type === 'text') {
                    valueForm[element.name] = element.value;
                }
            } catch (err) {
                if (!(err instanceof TypeError)) {
                    console.error(err);
                }
            }
            valueForm.password = document.getElementById('passwordField').value;
            dispatch(checkAuth(valueForm.login, valueForm.password));
        }
    };

    const classes = useStyles();

    return (
        <Container component="main" maxWidth="sm">
            {alert && <Alert severity="error">This is an error alert — check it out!</Alert>}
            {auth ? <Redirect push to='/transport' /> : <></>}
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <h4 className={classes.h5}>Sign in</h4>
                <form onSubmit={onSubmit} className={classes.form}>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Login"
                        name="login"
                        type='text'
                        autoFocus
                        className={classes.textInput}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label="Password"
                        type="password"
                        id="passwordField"
                        className='password'
                    />
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        className={classes.submit}
                    >
                        Sign In
                    </Button>
                    <NavLink to='/create_account'>У вас нет аккаунта? Регистрация</NavLink>
                </form>
            </div>
        </Container>
    );
};

Auth.propTypes = {
    setIsAuth: PropTypes.func,
    serverData: PropTypes.object
};

const mapStateToProps = (state) => ({
    auth: state.auth.isAuth,
    alert: state.auth.isAlert,
});

export default connect(mapStateToProps)(Auth);
