import React, {useState} from 'react';
import {refreshPassword} from './utils';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import Avatar from '@material-ui/core/Avatar';
import style from './RefreshPassword.module.css'

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(13),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        backgroundColor: 'white',
        borderRadius: '20px',
        padding: '2% 8%'
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.success.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

const RefreshPassword = () => {
    const [password, setPassword] = useState({});
    const [alert, setAlert] = useState(false);
    const classes = useStyles();

    const onChangePassword = (e) => {
        const {target} = e;
        e.preventDefault();
        setPassword({ ...password, [target.name]: target.value});
    };

    const onSubmit = (e) => {
        e.preventDefault();
        const accessToken = localStorage.getItem('accessToken');
        if (!accessToken) {
            return
        }
        refreshPassword(accessToken, password.oldPassword, password.newPassword)
        .then(data => {
            if (data.status) {
                setAlert(false)
            } else {
                setAlert(true);
                setTimeout(() => {
                    setAlert(false);
                }, 1000)
            }
        })
    };

    return (
        <Container component="main" maxWidth="xs">
            {alert && <Alert severity="warning">Пароль не изменён.</Alert>}
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <AccountBoxIcon />
                </Avatar>
                <h4 className={style.h5}>Обновление пароля</h4>
                <form onSubmit={onSubmit} className={classes.form}>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Старый пароль"
                        name="oldPassword"
                        type='text'
                        id='old-password'
                        autoFocus
                        className={classes.textInput}
                        onChange={onChangePassword}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="newPassword"
                        label="Новый пароль"
                        type="text"
                        id="new-password"
                        className='password'
                        onChange={onChangePassword}
                    />
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        className={classes.submit}
                    >
                        Обновить
                    </Button>
                </form>
            </div>
        </Container>
    );
};

export default RefreshPassword;
