import {domain} from "../../config";

export const getTransportNumber = (city, transportType) => {
    return fetch(`${domain}/api/${city}/transport_numbers?transport_type=${transportType}`)
        .then((response) => response.json());
};

export const getBusStopInformation = (city, transport_number, selection_bus_stop, time, sort, transport_type) => {
    const url = `${domain}/api/${city}/get_situation/${transport_number}?selection_bus_stop=${selection_bus_stop}&time=${time}&sort=${sort}&transport_type=${transport_type}`;
    return fetch(url).then((response) => response.json());
};

export function getDefaultTableOptions(defaulTableValue) {
    const value = JSON.parse(localStorage.getItem('tableValue'));
    if (value === null) {
        localStorage.setItem('tableValue', JSON.stringify(defaulTableValue));
        return defaulTableValue;
    }
    return value;
}