import {PUT_AUTH, REMOVE_AUTH, ADD_ALERT, REMOVE_ALERT} from '../types'

const initialState = {
    isAuth: false,
    isAlert: false,
};

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case PUT_AUTH:
            return ({ ...state, isAuth: true });
        case REMOVE_AUTH:
            return ({ ...state, isAuth: false });
        case ADD_ALERT:
            return ({...state, isAlert: true});
        case REMOVE_ALERT:
            return ({...state, isAlert: false});
        default: return state
    }
};