import React from 'react';
import Button from '@material-ui/core/Button';
import { NavLink } from 'react-router-dom';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import style from './Header.module.css';
import {removeAuth} from "../../redux/Actions/authAction";
import { connect } from 'react-redux'

const Account = ({auth, dispatch}) => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const logout = () => {
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        dispatch(removeAuth());
        setAnchorEl(null);
    };

    return (
        <>
        {auth ? <Button
            aria-controls="menu-appbar"
            style={{
                fontSize: "15px",
                fontWeight: 600,
                border: "none",
                color: '#fff',
            }}
            onClick={handleMenu}
            color="inherit"
            size='medium'
            variant='outlined'
            className={style.profile}
            key='account'>
                Аккаунт
        </Button>:
            <NavLink to='/auth' key='auth' className={style.auth}>Вход</NavLink>}

            <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={open}
                onClose={handleClose}>
                <MenuItem onClick={handleClose}><NavLink to='/user/refresh_password'>Изменить пароль</NavLink></MenuItem>
                <MenuItem onClick={handleClose}><NavLink to='/user/add_bus_stop'>Добавить остановку</NavLink></MenuItem>
                <MenuItem onClick={logout}><NavLink to='/auth'>Выйти</NavLink></MenuItem>
            </Menu>
        </>
    );
};

const stateToProps = (state) => ({
    auth: state.auth.isAuth,
});

export default connect(stateToProps)(Account);
